@import url(https://fonts.googleapis.com/css?family=Leckerli+One&display=swap);
/* BEM */

footer {
  align-items: center;
  color: #721c24;
  display: flex;
  flex-direction: column;
  font-size: 12px;
  margin-bottom: 8px;
}

footer.healthy {
  color: #155724;
}

footer a {
  color: rgb(29, 161, 242);
  font-weight: bold;
  text-decoration: none;
}

footer ul {
  display: flex;
}

footer li {
  margin: 0 8px;
}

footer p {
  margin: 0 0 4px 0;
}

.LinefeedPicker__li {
  align-items: center;
  display: flex;
  flex-direction: row;
  margin: 0;
}

.LinefeedPicker__linefeed {
  text-align: center;
}

.LinefeedPicker__ul {
  display: flex;
}

/* BEM */

.NavBar {
  align-items: center;
  display: flex;
  flex-direction: column;
  padding: 8px 0;
  width: 100%;
}

.NavBar img {
  border: 1px solid rgb(204, 214, 221);
}

/* BEM */

.ToastNotification {
  border: 1px solid;
  border-radius: 4px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 32px;
  width: 100%;
}

.ToastNotification button {
  background: transparent;
  border: 0;
  margin: 8px 8px 8px 0;
  width: 32px;
}

.ToastNotification span {
  margin: 8px 0 8px 8px;
}

.ToastNotification--danger {
  background-color: #f8d7da;
  border-color: #f5c6cb;
  color: #721c24;
}

.ToastNotification--success {
  background-color: #d4edda;
  border-color: #c3e6cb;
  color: #155724;
}

/* BEM */

article,
button {
  color: rgb(20, 23, 26);
}

button,
input,
textarea {
  background-color: rgb(230, 236, 240);
  border: 1px solid rgb(204, 214, 221);
  font-size: 16px;
}

/* --- */

article {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 768px;
  padding: 8px;
  width: 100%;
}

body {
  background-color: rgb(29, 161, 242, 0.125);
}

button {
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  width: 128px;
}

form {
  display: flex;
  flex-direction: column;
  width: 100%;
}

form > * {
  margin-bottom: 32px;
}

h1 {
  color: rgb(20, 23, 26);
  font-family: 'Leckerli One', cursive;
  font-size: 40px;
  margin: 16px 0;
}

li {
  display: flex;
  flex-direction: column;
  margin-bottom: 32px;
  width: 100%;
}

textarea {
  border-radius: 4px;
  margin-bottom: 4px;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.App__button {
  align-self: center;
  background-color: rgb(29, 161, 242);
  color: white;
}

.App__button--disabled {
  background-color: rgb(204, 214, 221);
  color: rgb(20, 23, 26);
  cursor: default;
}

.App__counter {
  color: gray;
  text-align: right;
}

.App__small {
  align-items: center;
  display: flex;
}

body {
  margin: 0;
  padding: 0;
}

body,
input,
textarea {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  align-items: center;
  display: flex;
  flex-direction: column;
}

